@keyframes opacityout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }

}

.todo-enter {
  opacity: 0.01;
}

.todo-enter.todo-enter-active {
  opacity: 1;
  transition: opacity .5s ease-in;
}

.todo-leave {
  opacity: 1;
}

.todo-leave.todo-leave-active {
  opacity: 0.01;
  transition: opacity .3s ease-out;
}


.item {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  position: relative;
  list-style: none;
  font-size: 22px;
  font-weight: 600;
  border: 2px solid #111111;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 15px;
  z-index: 1;
  min-width: 700px;
  max-width: 900px;
  opacity: 1;
  transition: all .4s ease;

  strong {
    margin-right: 5px;
  }

  .closed {
    animation: .5s opacityout forwards;
  }
}



.time {
  position: absolute;
  top: 10%;
  right: 30px;
  font-size: 16px;
  margin: 0;
}



.deleteButton {
  //background-color: black;
  position: absolute;
  right: 10px;
  top: 25%;
  outline: 0;
  border: none;
  width: 0;
  height: 0;
  background-color: transparent;

  &:before,
  &:after {
    position: absolute;
    content: '';
    width: 2px;
    height: 15px;
    background-color: black;
    display: block;
    margin: 0;
    padding: 2px;
    transition: all .4s ease;
  }

  &:before {
    transform: rotate(45deg);
  }

  &:after {
    transform: rotate(-45deg)
  }

  &:hover {
    cursor: pointer;
    opacity: 0.7;
  }

  &:hover:before,
  &:hover:after {
    background-color: #049eec;
  }

  &:active:before,
  &:active:after {
    background-color: #014567;

  }
}



li span {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.solvedCheckout  {
  display: flex;

  button {
    outline: none;
    border: 2px solid black;
    border-radius: 50%;
    height: 1.2em;
    width: 1.2em;
    margin-right: 0.3em;
    background-color: floralwhite;
    transition: all 0.3s ease;
    cursor: pointer;

    &:hover {
      background-color: #049eec;
    }

    &.active {
      background-color: transparent;
      border: transparent;
    }

    &:before {
      content: '\2713';
      position: absolute;
      width: 10px;
      left: 1%;
      top: 15%;
      font-size: 20px;
      font-weight: 1000;
      transition: all .4s ease;
      color: transparent;
    }

    &.active:before {
      display: inline;
      color: #111111;
      opacity: 1;
    }
  }
}

.title {
  display: flex;
  flex-wrap: wrap;
  margin-right: 70px;
  min-width: 50%;
  max-width: 700px;
}

.title.solved {
  text-decoration: line-through;
}

