html {
  background: linear-gradient(#da75d6 40%, #938e49, #8e7936) no-repeat center;
  background-size: cover;
  min-height: 100vh;
}

.AppWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 5rem;
  max-width: 60%;
  margin: 0 auto;

  h1, h2 {
    text-align: center;
  }

  .formWrapper {
    margin: 0 auto;
    margin-bottom: 0.5rem;

    input {
      background: transparent;
      border: 3px solid black;
      border-radius: 20px;
      color: #014567;
      padding: 5px 20px;
      outline: none;
      font-size: 25px;
      transition: all .4s ease;

      &::placeholder {
        color: black;
      }

      &:hover::placeholder,
      &:focus::placeholder {
        color: #014567;
        transition: all .3s ease;
      }

      &:hover,
      &:focus {
        border-color: #006395;
      }
    }

    button {
      background: transparent;
      position: relative;
      //border: 3px solid black;
      border: none;
      padding: 5px 0;
      line-height: 1.5;
      margin-left: 10px;
      font-size: 20px;
      outline: none;
      cursor: pointer;
      font-weight: bold;
      transition: all .2s ease;

      &:hover {
        //transform: scale(1.02);
        color: #014567;
      }

      &::after {
        display: block;
        position: absolute;
        left: 0;
        width: 0;
        height: 2px;
        background-color: #014567;
        content: '';
        transition: width .3s ease-out;
      }

      &:hover::after,
      &:focus::after {
        width: 100%;
      }
    }
  }
}