@keyframes slidein {
  from {
    left: -50%;
    opacity: .2;
  }

  to {
    left: 28%;
    opacity: 1;
  }
}

@keyframes opacityin {
  from {
    opacity: 0;
    display: none;
  }

  to {
    opacity: 1;
    display: flex;
  }
}

@keyframes opacityout {
  from {
    opacity: 1;
    display: flex;
  }

  to {
    opacity: 0;
    z-index: -1000;
  }

}

@keyframes slideout {
  from {
    top: 0;
    opacity: 1;
  }

  to {
    top: -100%;
    opacity: .2;
  }
}

.ModalButton {
  outline: none;
  background: none;
  border: 3px solid black;
  border-radius: 15px;
  padding: 5px 20px;
  max-width: 150px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  transition: all .4s ease;

  &:hover {
    background: rgba(4, 158, 236, 0.3);
  }

  &:active {
    background-color: #ffffff;
    color: rgba(4, 158, 236, 0.3);
    transform: scale(0.9);
  }
}

.modalWindow-body .ModalButton {
  margin-top: 5%;
}

.modalWindow {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  padding-top: 5rem;
  z-index: 100;
  animation: .5s forwards opacityin;
}

.hidden {
  animation: .5s forwards opacityout;
}

.modalWindow-body {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  width: 40%;
  border: 3px dashed #2a0144;
  border-radius: 10px;
  background: #fff;
  height: 200px;
  left: -45%;


  &.opened {
    animation: .8s forwards slidein;
  }

  //&.closed {
  //  animation: .8s forwards slideout;
  //  left: 28%;
  //}

  //h1 {
  //  margin-right: 65%;
  //}

  p {
    font-size: 20px;
    //margin-right: 50%;
    margin-top: 0;
  }

}


@media (max-width: 1366px) {
  .ModalButton:not(.modalWindow-body .ModalButton) {
    position: absolute;
    bottom: 30%;
    left: 45%;
  }
}


